// import React, { useState } from 'react';
import React, { Component } from 'react';
import axios from "axios";
import Joke from './Joke';
import "./DADJOKE.css"
// import "./JokeList.css"
// import uuid from 'uuid';
import { v4 as uuidv4 } from 'uuid';
class DADJOKE extends Component{

    static defaultProps={
         totalJokes:10
    }
  

    constructor(props){
        super(props);
        this.state={  

             jokes:JSON.parse(window.localStorage.getItem("jokes") || "[]"),
             loading:false
            //  jokes:JSON.parse("[]")
        };
        this.handleVote=this.handleVote.bind(this);
        this.handleClick=this.handleClick.bind(this);
    }

  
  async getJokes(){
    var data=[];

    while(data.length<this.props.totalJokes){
         var element=await axios.get("https://icanhazdadjoke.com",{
             headers:{Accept:"application/json"}
         });
         var joke_element=element.data.joke;
         data.push({id:uuidv4(),joke:joke_element,votes:0});
        //  console.log(element.data.joke);
    }
  
    this.setState({loading:false,jokes:[...this.state.jokes,...data]},()=> window.localStorage.setItem("jokes",JSON.stringify(this.state.jokes)));
  // window.localStorage.setItem("jokes",JSON.stringify(data));

  }  

   componentDidMount(){
      if(this.state.jokes.length==0){
        this.getJokes();
      }
  }

handleVote(id,delta){
 this.setState(state=>({
  jokes:state.jokes.map(j=>
    j.id===id?{...j,votes:j.votes+delta}:j
  )
}),
()=> window.localStorage.setItem("jokes",JSON.stringify(this.state.jokes)));
   
}

handleClick(){
  this.setState({loading:true});
  this.getJokes();
}


render(){

if(this.state.loading){
  return(
    
    <div className="JokeList-spinner"> 
       <i className="far fa-8x fa-laugh fa-spin"/>
       <h1 className="JokesList-title">Loading...</h1>
    </div>

  );
}

return(

  <div className="JokeList">


    <div className="JokeList-sidebar">
      {/* <div > */}
            <h1 className="JokeList-title"><span>Dad's</span> Jokes</h1>
      {/* </div> */}
      <img src="data:image/jpeg;base64,"/>
      <button className="JokeList-getmore" onClick={this.handleClick}>New Jokes</button>
    </div>
     
    {/* <div>{j.joke+" "+j.votes}</div> */}
        <div className="JokeList-jokes">
           <ul>
              {this.state.jokes.map(j=>(
               <Joke id={j.id} votes={j.votes} joke={j.joke} upvalue={1} upvote={this.handleVote} downvalue={-1} downvote={this.handleVote}/>
                ))}
            </ul>     
        </div>
        
    </div>

);

}


}

export default DADJOKE;
